
html, body, #app, #app>div {
  height: 100%;
  background-image: -webkit-gradient(linear, left top, left bottom, from(silver), to(darkblue));
  background-image: -webkit-linear-gradient(silver, darkblue);
  background-image: -o-linear-gradient(silver, darkblue);
  background-image: linear-gradient(silver, darkblue);

  padding: 0px;
  font-family: sans-serif;
}

#root {
  height: 100%;
}


